import React from 'react'
import img from '../public/img/not.png'

export default class App extends React.Component {
  render() {
    return (
      <div class="wrap">
        <div class="logo">
          <p>OOPS! - Could not Find it</p>
          <img src={img} />
          <div class="sub">
            <p><a href="#">Back</a></p>
          </div>
        </div>
      </div>
    )
  }
}



